<h3 className="">{"Dividers"}</h3>
{"\n"}
<p className="">{"Dividers must have the "}
<code className="">{"divider"}</code>
{" attribute and it is recommended to add\n"}
<code className="">{"role=\"separator\""}</code>
{" for screen readers. There are 3 variants of dividers,\nfull-width (default), padded (respects list padding), and inset (left-padding\nrespects avatar and icon paddding). These variants can be mixed."}</p>
{"\n"}
<Example>
    <mwc-list>
        <mwc-list-item>Item 0</mwc-list-item>
        <li divider role="separator" />
        <mwc-list-item>Item 1</mwc-list-item>
        <li divider padded role="separator" />
        <mwc-list-item>Item 2</mwc-list-item>
        <li divider padded role="separator" />
        <mwc-list-item>Item 3</mwc-list-item>
        <li divider padded role="separator" />
        <mwc-list-item graphic="avatar">
            <span>avatar item</span>
            <mwc-icon slot="graphic">folder</mwc-icon>
        </mwc-list-item>
        <li divider inset padded role="separator" />
        <mwc-list-item graphic="avatar">
            <span>avatar item</span>
            <mwc-icon slot="graphic">folder</mwc-icon>
        </mwc-list-item>
    </mwc-list>
</Example>
