<h3 className="">{"Item Noninteractive"}</h3>
{"\n"}
<p className="">{"Setting a list-item to non-interactive will disable focus and pointer events on\nthe item, and it will no longer be considered for selection."}</p>
{"\n"}
<Example>
    <style>
        {`.inverted {
            background-color: gray;
            color: white;
        }`}
    </style>
    <mwc-list>
        <mwc-list-item twoline graphic="avatar" noninteractive>
            <span>User Name</span>
            <span slot="secondary">user@domain.tld</span>
            <mwc-icon slot="graphic" className="inverted">
                tag_faces
            </mwc-icon>
        </mwc-list-item>
        <li divider role="separator" />
        <mwc-list-item graphic="icon">
            <slot>FAQ</slot>
            <mwc-icon slot="graphic">help_outline</mwc-icon>
        </mwc-list-item>
        <mwc-list-item graphic="icon">
            <slot>Sign out</slot>
            <mwc-icon slot="graphic">exit_to_app</mwc-icon>
        </mwc-list-item>
    </mwc-list>
</Example>
