<h3 className="">{"Styled No-ripple"}</h3>
{"\n"}
<p className="">{"For more-control on styling, you may want to disable the ripple. Internally\n"}
<code className="">{"list-item"}</code>
{" uses "}
<code className="">{"mwc-ripple"}</code>
{". You can make the ripple invisible using its\ncustom properties detailed in "}
<a href="components/ripple" className=""><code className="">{"mwc-ripple"}</code>
{"'s documentation"}</a>
{". In this case,\nit may be simple to just set "}
<code className="">{"--mdc-ripple-color"}</code>
{" to "}
<code className="">{"transparent"}</code>
{"."}</p>
{"\n"}
<Example>
    <style>
        {`/* disable ripple */
        #styledr {
            --mdc-ripple-color: transparent;
        }
        #styledr > * {
            transition: background-color 0.2s, color 0.2s;
        }
        #styledr [selected] {
            background-color: rgb(33, 33, 33);
            color: white;
        }
        #styledr [mwc-list-item]:not([selected]):hover,
        #styledr [mwc-list-item]:not([selected]):focus {
            background-color: rgba(33, 33, 33, 0.3);
        }
        #styledr [mwc-list-item]:not([selected]):active {
            background-color: rgba(33, 33, 33, 0.4);
        }
        #styledr [mwc-list-item][selected]:hover,
        #styledr [mwc-list-item][selected]:focus {
            background-color: rgba(33, 33, 33, 0.9);
        }
        #styledr [mwc-list-item][selected]:active {
            background-color: rgba(33, 33, 33, 0.8);
        }`}
    </style>
    <mwc-list id="styledr">
        <mwc-list-item selected>Item 0</mwc-list-item>
        <mwc-list-item>Item 1</mwc-list-item>
        <mwc-list-item>Item 2</mwc-list-item>
        <mwc-list-item>Item 3</mwc-list-item>
    </mwc-list>
</Example>
